@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
  src: url("../fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2");
  src: url("../fonts/Montserrat-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2");
  src: url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2");
  src: url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: "Montserrat", "Arial", sans-serif;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.benefits {
  padding: 120px 0;
  background-image: linear-gradient(rgba(227, 236, 246, 0.9), rgba(227, 236, 246, 0.9)), url("../img/bg-benefits.jpg");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .benefits {
    background-image: linear-gradient(rgba(227, 236, 246, 0.9), rgba(227, 236, 246, 0.9)), url("../img/bg-benefits@2x.jpg");
  }
}

.benefits__title {
  margin: 0;
  margin-bottom: 60px;
  padding: 0;
  text-align: center;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  color: #011c40;
}

.benefits__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 40px;
  row-gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .benefits__list {
    grid-template-columns: 1fr;
  }
}

.benefits__item {
  position: relative;
  padding: 87px 50px 50px 30px;
  background-color: #f9fbfd;
}
@media (max-width: 1023px) {
  .benefits__item {
    padding: 76px 30px 25px 20px;
  }
}
.benefits__item::after {
  position: absolute;
  content: "";
  top: 30px;
  left: 30px;
}
@media (max-width: 1023px) {
  .benefits__item::after {
    top: 25px;
    left: 20px;
  }
}
.benefits__item--ship::after {
  width: 27px;
  height: 32px;
  background-image: url("../img/svg/ship.svg");
}
@media (max-width: 1023px) {
  .benefits__item--ship::after {
    width: 21px;
    height: 26px;
    background-image: url("../img/svg/ship-small.svg");
  }
}
.benefits__item--tick::after {
  width: 38px;
  height: 32px;
  background-image: url("../img/svg/tick.svg");
}
@media (max-width: 1023px) {
  .benefits__item--tick::after {
    width: 27px;
    height: 26px;
    background-image: url("../img/svg/tick-small.svg");
  }
}
.benefits__item--trip::after {
  width: 44px;
  height: 32px;
  background-image: url("../img/svg/trip.svg");
}
@media (max-width: 1023px) {
  .benefits__item--trip::after {
    width: 36px;
    height: 26px;
    background-image: url("../img/svg/trip-small.svg");
  }
}

.benefits__item-title {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #011c40;
}

.benefits__item-text {
  font-size: 14px;
  line-height: 20px;
  color: #011c40;
}

.catalog {
  padding-top: 95px;
  padding-bottom: 120px;
}

.catalog__title {
  text-align: center;
  margin: 0;
  margin-bottom: 49px;
  padding: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  color: #011c40;
}

.catalog__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  margin: 0;
  padding: 0;
  padding-left: 15px;
  padding-right: 15px;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .catalog__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 767px) {
  .catalog__list {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}

.catalog__item {
  position: relative;
}

.catalog__cruises-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 28px 25px 35px 25px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  z-index: 0;
  overflow-y: scroll;
  background-color: #011c40;
  box-shadow: 0 4px 35px rgba(1, 28, 64, 0.15);
}

.catalog__item:hover .catalog__cruises-content {
  z-index: 2;
}

.catalog__cruises-content h3 {
  margin: 0;
  margin-bottom: -3px;
  padding: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #f9fbfd;
}

.catalog__cruises-description-wrapper dt {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #f9fbfd;
}

.catalog__cruises-description-wrapper dd {
  margin: 0;
  margin-bottom: 12px;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #d8d8d9;
}

.catalog__cruises-button {
  padding-top: 8px;
  padding-bottom: 9px;
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  background-color: #f9fbfd;
}

.catalog__cruises-button:focus-within {
  z-index: 2;
  border: 1px solid #ff1553;
}

.catalog__cruises-img {
  width: 100%;
  max-height: 100%;
  object-fit: contain;
  aspect-ratio: 260/386;
}

.catalog__cruises-intro {
  position: relative;
}
@media (max-width: 1023px) {
  .catalog__cruises-intro {
    min-height: 386px;
    max-height: 386px;
    overflow: hidden;
  }
}
@media (max-width: 767px) {
  .catalog__cruises-intro {
    min-width: 270px;
    max-width: 270px;
  }
}

.catalog__cruises-intro h3 {
  position: absolute;
  bottom: 0;
  left: 30px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .catalog__cruises-intro h3 {
    left: 35px;
  }
}

.contacts__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  min-height: 546px;
}
@media (max-width: 1023px) {
  .contacts__wrapper {
    column-gap: 30px;
    min-height: 535px;
  }
}
@media (max-width: 767px) {
  .contacts__wrapper {
    grid-template-columns: 1fr;
    min-height: 703px;
  }
}

@media (max-width: 1023px) {
  .contacts__image {
    min-height: 335px;
    object-fit: cover;
  }
}

.contacts__inner-wrapper {
  margin-left: 85px;
}
@media (max-width: 1023px) {
  .contacts__inner-wrapper {
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .contacts__inner-wrapper {
    grid-row: 1/2;
  }
}

.contacts__text {
  margin-bottom: 45px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #011c40;
}
@media (max-width: 1023px) {
  .contacts__text {
    margin-bottom: 40px;
  }
}

.contacts__address,
.contacts__phone {
  position: relative;
  padding-left: 28px;
  font-size: 16px;
  line-height: 24px;
  font-style: normal;
  color: #011c40;
}

.contacts__address {
  margin-bottom: 33px;
}

.contacts__inner-wrapper svg {
  position: absolute;
}

.contacts__map {
  min-height: 306px;
}

.description__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 88px;
  margin-bottom: 145px;
}
@media (max-width: 1023px) {
  .description__wrapper {
    column-gap: 25px;
    margin-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .description__wrapper {
    grid-template-columns: 1fr;
    margin-bottom: 85px;
  }
}

.description__title {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
@media (max-width: 1023px) {
  .description__title {
    margin-bottom: 30px;
    font-size: 26px;
    line-height: 36px;
  }
}
@media (max-width: 767px) {
  .description__title {
    font-size: 24px;
    line-height: 34px;
  }
}

.description__text {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
@media (max-width: 767px) {
  .description__text {
    margin-bottom: 50px;
  }
}

.footer {
  min-height: 138px;
  display: flex;
  align-items: center;
  background-color: #011c40;
}
@media (max-width: 1023px) {
  .footer {
    min-height: 120px;
  }
}
@media (max-width: 767px) {
  .footer {
    min-height: 283px;
  }
}

.footer__wrapper {
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .footer__wrapper {
    flex-direction: column;
    align-items: start;
  }
}

.footer__link {
  margin-right: 40px;
}
@media (max-width: 1023px) {
  .footer__link {
    margin-right: 25px;
  }
}
@media (max-width: 767px) {
  .footer__link {
    margin-bottom: 30px;
  }
}

.footer__nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .footer__nav-list {
    flex-direction: column;
    margin-bottom: 30px;
  }
}

.footer__nav-item {
  margin-right: 33px;
}
@media (max-width: 1023px) {
  .footer__nav-item {
    margin-right: 23px;
  }
}

.footer__nav-item:last-child {
  margin-right: 0;
}

.footer__nav-link {
  position: relative;
  padding-bottom: 10px;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .footer__nav-link {
    font-size: 12px;
    line-height: 15px;
  }
}
.footer__nav-link::before {
  position: absolute;
  content: "";
  width: 0;
  height: 1px;
  bottom: 0;
  left: 50%;
  background-color: #f9fbfd;
  transition: width, left, 0.3s ease;
}
@media (hover: hover) {
  .footer__nav-link:hover:not(.focus-visible)::before {
    width: 100%;
    bottom: 0;
    left: 0;
  }
}

.footer__social-list {
  margin: 0;
  margin-left: auto;
  padding: 0;
  list-style-type: none;
  display: flex;
}
@media (max-width: 767px) {
  .footer__social-list {
    margin-left: 0;
  }
}

.footer__social-item {
  margin-right: 27px;
}
@media (max-width: 1023px) {
  .footer__social-item {
    margin-right: 22px;
  }
}

.footer__social-link svg {
  fill: #f9fbfd;
}
@media (hover: hover) {
  .footer__social-link svg:hover:not(.focus-visible):hover:not(.focus-visible) {
    fill: rgba(51, 182, 255, 0.6);
  }
  .footer__social-link svg:hover:not(.focus-visible).focus-visible:focus {
    fill: rgba(51, 182, 255, 0.9);
  }
}
.footer__social-link svg.focus-visible:focus:hover:not(.focus-visible) {
  fill: rgba(51, 182, 255, 0.6);
}
.footer__social-link svg.focus-visible:focus.focus-visible:focus {
  fill: rgba(51, 182, 255, 0.9);
}

.form {
  display: flex;
  align-items: center;
  min-height: 555px;
  background-image: url("../img/bg-form-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .form {
    background-image: url("../img/bg-form-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .form {
    min-height: 805px;
  }
}
@media (max-width: 767px) {
  .form {
    min-height: 760px;
  }
}

.form__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}
@media (max-width: 1023px) {
  .form__wrapper {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}

.form__title {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  color: #011c40;
}
@media (max-width: 1023px) {
  .form__title {
    margin-bottom: 25px;
    text-align: center;
    font-size: 34px;
    line-height: 51px;
  }
}
@media (max-width: 767px) {
  .form__title {
    font-size: 24px;
    line-height: 31px;
  }
}

.form__text {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #011c40;
}
@media (max-width: 1023px) {
  .form__text {
    max-width: 352px;
    margin-bottom: 60px;
    text-align: center;
    font-size: 18px;
    line-height: 27px;
  }
}
@media (max-width: 767px) {
  .form__text {
    margin-bottom: 40px;
    font-size: 16px;
    line-height: 22px;
  }
}

.form__user {
  display: flex;
  flex-direction: column;
  max-width: 403px;
  padding: 50px;
  background-color: #011c40;
  box-shadow: 0 4px 45px rgba(1, 28, 64, 0.15);
}
@media (max-width: 767px) {
  .form__user {
    max-width: 290px;
    padding: 40px 30px;
  }
}

.form__user-text {
  margin-bottom: 35px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .form__user-text {
    font-size: 18px;
    line-height: 25px;
    text-align: center;
  }
}

.form__user input {
  width: 100%;
  margin: 0;
  padding: 11px 0 11px 16px;
  margin-bottom: 20px;
  background-color: #f9fbfd;
  border: none;
}
@media (hover: hover) {
  .form__user input:hover:not(.focus-visible):hover:not(.focus-visible) {
    background-color: rgba(51, 182, 255, 0.9);
    color: #f9fbfd;
  }
}
.form__user input.focus-visible:focus:hover:not(.focus-visible) {
  background-color: rgba(51, 182, 255, 0.9);
  color: #f9fbfd;
}
.form__user input.focus-visible:focus {
  background-color: rgba(51, 182, 255, 0.6);
  color: #f9fbfd;
}
.form__user input.focus-visible:active.focus-visible:active {
  background-color: rgba(51, 182, 255, 0.6);
  color: #f9fbfd;
}

.form__user input::placeholder {
  font-size: 14px;
  line-height: 21px;
  color: rgba(1, 28, 64, 0.7);
}

.form__user input[type=checkbox] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  padding: 0;
  margin-bottom: 0;
}

.form__agreement {
  position: relative;
  display: block;
  padding-left: 23px;
  margin-bottom: 35px;
  cursor: pointer;
  font-size: 10px;
  line-height: 13px;
  color: #f9fbfd;
}
.form__agreement::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}

input[type=checkbox]:checked ~ .form__agreement::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  background-image: url("../img/svg/tick-form.svg");
}

input[type=checkbox]:hover ~ .form__agreement::before,
input[type=checkbox]:focus ~ .form__agreement::before {
  background-color: rgba(51, 182, 255, 0.6);
  outline: 2px solid #000000;
}

.form__button {
  padding-top: 8px;
  padding-bottom: 9px;
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
}
@media (hover: hover) {
  .form__button:hover:not(.focus-visible):hover:not(.focus-visible) {
    background-color: rgba(51, 182, 255, 0.9);
    color: #f9fbfd;
  }
}
.form__button.focus-visible:focus:hover:not(.focus-visible) {
  background-color: rgba(51, 182, 255, 0.9);
  color: #f9fbfd;
}
.form__button.focus-visible:focus {
  background-color: rgba(51, 182, 255, 0.6);
  color: #f9fbfd;
}
.form__button.focus-visible:active.focus-visible:active {
  background-color: rgba(51, 182, 255, 0.6);
  color: #f9fbfd;
}

.header {
  margin-top: 35px;
  margin-bottom: -100px;
  z-index: 2;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__image {
  margin-right: 30px;
}
@media (max-width: 1023px) {
  .header__image {
    width: 38px;
    height: 20px;
  }
}

.header__link path {
  fill: #f9fbfd;
}

.header__nav-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .header__nav-list {
    display: none;
  }
}

.header__nav-item {
  margin-right: 33px;
}

.header__nav-item:last-child {
  margin-right: 0;
}

.header__nav-link {
  position: relative;
  padding-bottom: 10px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
}
.header__nav-link::before {
  position: absolute;
  content: "";
  width: 0;
  height: 1px;
  bottom: 0;
  left: 50%;
  background-color: #f9fbfd;
  transition: width, left, 0.3s ease;
}
@media (hover: hover) {
  .header__nav-link:hover:not(.focus-visible)::before {
    width: 100%;
    bottom: 0;
    left: 0;
  }
}
@media (max-width: 1023px) {
  .header__nav-link {
    font-size: 14px;
    line-height: 17px;
  }
}

.header__button {
  position: relative;
  display: none;
}
@media (max-width: 767px) {
  .header__button {
    display: block;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    border: none;
  }
  .header__button::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    top: -5px;
    left: 0;
    background-color: #f9fbfd;
  }
  .header__button::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    bottom: -5px;
    left: 0;
    background-color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .header.header__menu--opened {
    min-width: 285px;
    position: absolute;
    top: -36px;
    height: 100vh;
    right: 0;
    background-color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .header__menu--opened .header__wrapper {
    flex-direction: column;
  }
}

@media (max-width: 767px) {
  .header__menu--opened .header__image {
    margin-top: 20px;
    margin-bottom: 39px;
  }
}

@media (max-width: 767px) {
  .header__menu--opened .header__nav-list {
    display: block;
    margin-left: 9px;
  }
}

@media (max-width: 767px) {
  .header__menu--opened .header__nav-item {
    margin-bottom: 26px;
  }
}

@media (max-width: 767px) {
  .header__menu--opened .header__link path {
    fill: #011c40;
  }
}

@media (max-width: 767px) {
  .header__menu--opened .header__vav-link {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .header__menu--opened .header__button {
    position: absolute;
    top: 36px;
    right: 6px;
    background-color: transparent;
  }
  .header__menu--opened .header__button::after {
    width: 18px;
    transform: rotate(45deg);
    background-color: #011c40;
  }
  .header__menu--opened .header__button::before {
    width: 18px;
    top: -5px;
    transform: rotate(135deg);
    background-color: #011c40;
  }
}

@media (max-width: 767px) {
  .header__menu--closed .header__nav-list {
    display: none;
  }
}

